/* ==========================================================================
   Layouts Market
   ========================================================================== */

.market {
  padding: 96px 0 64px;
  background-color: color(secondary);
  background-image: url(../img/bg/circle-top.png);
  background-repeat: no-repeat;
  background-size: 686px auto;
  background-position: 151px top;
  @include clearfix;
  @include small {
    padding: 48px 0 32px;
  }
  &__head {
    max-width: 758px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 96px;
    @include medium {
      margin-bottom: 48px;
    }
    &__title {
      @include font-size(h2);
      color: color(white);
      font-weight: fw(bold);
      margin-bottom: 0;
      @include extra-small {
        @include font-size(h4);
      }
    }
  }
  &__body {
    @include clearfix;
  }
  &__img {
    float: right;
    width: 48%;
    padding-bottom: 129px;
    position: relative;
    @include medium {
      width: 100%;
      float: none;
      padding-left: 0;
      margin-bottom: 24px;
    }
    @include extra-small {
      padding-bottom: 64px;
      margin-bottom: 16px;
    }
    &__btc {
      width: 100%;
    }
    &__increase {
      position: absolute;
      width: 174px;
      bottom: 0;
      left: 64px;
      @include large {
          left: 54px;
      }
      @include extra-small {
        left: 32px;
        width: 100px;
      }
    }
    &__price {
      position: absolute;
      width: 169px;
      bottom: 60px;
      right: 0;
      @include extra-small {
        bottom: 30px;
        width: 100px;
      }
    }
  }
  &__txt {
    float: left;
    width: 52%;
    padding: 32px 0;
    @include medium {
      width: 100%;
      float: none;
      padding: 0;
    }
    &__title {
      @include font-size(h3);
      margin-bottom: 24px;
      color: color(white);
      @include extra-small {
        margin-bottom: 8px;
        @include font-size(h4);
      }
    }
    &__desc {
      @include font-size(h6);
      margin-bottom: 32px;
      max-width: 408px;
      color: color(grey, 1);
    }
  }
}